.App {
  text-align: center;
  margin: 20px;
}

h2 {
  color: #333;
}

form {
  margin: 20px 0;
}

label {
  display: block;
  margin: 10px 0;
}

textarea {
  width: 100%;
  height: 100px;
}

input[type="text"],
input[type="number"],
input[type="checkbox"] {
  margin: 5px 0;
}

button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
